<script>
	let clazz = '';
	export { clazz as class };
	export let color = 'secondary';
	export let pill = false;

	let classes = '';

	$: {
		const classNames = [];
		if (clazz) {
			classNames.push(clazz);
		}

		classNames.push('badge')
		classNames.push(`badge-${color}`);

		if (pill) {
			classNames.push('badge-pill');
		}
		classes = classNames.join(' ');
	}
</script>

<span class="{classes}">
	<slot />
</span>
